.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 620px;
    padding: 10px 120px 10px 120px;
}

.popular h1{
    color: #717171;
    font-size: 50px;
    font-weight: 600;
}

.popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.poppular-item {
    margin-top: 50px;
    display: flex;
    gap: 30px;
}

.poppular-item .item{
    width: 285px;
}
.poppular-item .item img,.item p, .item-prices{
    width: 95%;
}